:root {
  --ff-primary: Calibri, Arial, sans-serif;
  --clr-primary: 358 95 40;
  --black: 0 0 0;
  --white: 0 0 100;
  --blue: 216 98 52;
  --red: 354 70 54;
}
@media (prefers-reduced-motion: no-preference) {
  :root {
    --transition: all .3s ease-in-out;
  }
}
@media (prefers-reduced-motion: reduce) {
  :root {
    --transition: none;
  }
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
  box-sizing: border-box;
}

body {
  position: relative;
  font-family: var(--ff-primary);
  font-size: 1.125rem;
}

#scroll-container {
  height: 100vh;
  overflow: hidden;
}

table {
  caption-side: bottom;
  border-collapse: collapse;
}

tbody, td, tfoot, th, thead, tr {
  border-color: inherit;
  border-style: solid;
  border-width: 0;
}

th {
  text-align: inherit;
}

.table {
  width: 100%;
  margin-block-end: 0.0625rem;
  vertical-align: top;
  border-color: hsl(var(--black)/0.15);
  max-height: 100vh;
  overflow-y: auto;
}
.table > thead {
  vertical-align: bottom;
  position: sticky;
  z-index: 1;
  top: 0;
  background-color: hsl(var(--white));
  border: none;
}
.table > thead > tr {
  border-bottom: 1px solid hsl(var(--black)/0.15);
}
.table > thead > tr > th {
  border: 1px solid hsl(var(--black)/0.15);
  text-align: center;
  width: 33.3333%;
}
.table > tbody {
  vertical-align: inherit;
}
.table > :not(caption) > * > * {
  padding: 0.625rem;
  color: hsl(var(--black));
  background-color: hsl(var(--white));
  border-bottom-width: 0.0625rem;
}
.table > :not(caption) > * > *.red-highlight {
  color: hsl(var(--clr-primary));
  font-weight: 600;
  text-transform: uppercase;
}
.table-striped > tbody > tr:nth-of-type(odd) > * {
  background-color: hsl(var(--black)/0.05);
}
.table-striped > tbody > tr {
  border-bottom: 1px solid hsl(var(--black)/0.15);
}
.table-striped > tbody > tr > td {
  border: 1px solid hsl(var(--black)/0.15);
  text-align: center;
}

.edit-link {
  display: block;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

td:has(.edit-link):hover {
  transition: all 0.3s ease-in-out;
  background-color: rgba(0, 0, 0, 0.15) !important;
}

.cell-content {
  position: relative;
  padding-inline-end: 20px;
}
.cell-content .fa {
  position: absolute;
  top: 2px;
  right: 4px;
  font-size: 14px;
  opacity: 0;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease-in-out;
}

td:has(.edit-link):hover .fa {
  opacity: 1;
}

.course {
  font-style: italic;
  text-align: left !important;
}
.course-short {
  max-width: 150px;
  font-weight: 700;
  text-align: left !important;
}
.course-inactive {
  opacity: 0.45;
  background-color: #fff3cd;
}
.course-inactive::after {
  content: " (not offered)";
  font-style: italic;
  font-size: 0.85em;
  color: #856404;
}