@use '../util' as *;

table {
  caption-side: bottom;
  border-collapse: collapse;
}

// prettier-ignore
tbody, td, tfoot, th, thead, tr {
  border-color: inherit;
  border-style: solid;
  border-width: 0;
}

th {
  text-align: inherit;
}

.table {
  width: 100%;
  margin-block-end: rem(1);
  vertical-align: top;
  border-color: hsl(var(--black) / 0.15);
  max-height: 100vh;
  overflow-y: auto;

  > thead {
    vertical-align: bottom;
    position: sticky;
    z-index: 1;
    top: 0;
    background-color: hsl(var(--white));
    border: none;

    > tr {
      border-bottom: 1px solid hsl(var(--black) / 0.15);
    }

    > tr > th {
      border: 1px solid hsl(var(--black) / 0.15);
      text-align: center;
      width: 33.3333%;
    }
  }

  > tbody {
    vertical-align: inherit;
  }

  > :not(caption) > * > * {
    padding: rem(10);
    color: hsl(var(--black));
    background-color: hsl(var(--white));
    border-bottom-width: rem(1);
    // border: 1px solid hsl(var(--black) / 0.15);

    &.red-highlight {
      color: hsl(var(--clr-primary));
      font-weight: 600;
      text-transform: uppercase;
    }

    // &:nth-of-type(odd) {
    //   font-weight: 600;
    // }
  }

  &-striped {
    > tbody > tr:nth-of-type(odd) > * {
      background-color: hsl(var(--black) / 0.05);
    }
    > tbody > tr {
      border-bottom: 1px solid hsl(var(--black) / 0.15);
    }

    > tbody > tr > td {
      border: 1px solid hsl(var(--black) / 0.15);
      text-align: center;
    }
  }
}

.edit-link {
  display: block;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

td:has(.edit-link):hover {
  transition: all 0.3s ease-in-out;
  background-color: rgb(0 0 0 / 0.15) !important;
}

.cell-content {
  position: relative;
  padding-inline-end: 20px;

  .fa {
    position: absolute;
    top: 2px;
    right: 4px;
    font-size: 14px;
    opacity: 0;
    text-decoration: none;
    color: inherit;
    transition: all 0.3s ease-in-out;
  }
}

td:has(.edit-link):hover .fa {
  opacity: 1;
}

.course {
  font-style: italic;
  text-align: left !important;

  &-short {
    max-width: 150px;
    font-weight: 700;
    text-align: left !important;
  }

  &-inactive {
    opacity: 0.45;
    background-color: #fff3cd;

    &::after {
      content: ' (not offered)';
      font-style: italic;
      font-size: 0.85em;
      color: #856404;
    }
  }
}
