@use '../util' as *;

:root {
  --ff-primary: Calibri, Arial, sans-serif;
  --clr-primary: 358 95 40;
  --black: 0 0 0;
  --white: 0 0 100;
  --blue: 216 98 52;
  --red: 354 70 54;

  @media (prefers-reduced-motion: no-preference) {
    --transition: all .3s ease-in-out;
  }

  @media (prefers-reduced-motion: reduce) {
    --transition: none;
  }
}
