@use '../util' as *;

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
  box-sizing: border-box;
}

body {
  position: relative;
  font-family: var(--ff-primary);
  font-size: rem(18);
}

#scroll-container {
  height: 100vh;
  overflow: hidden;
}
